import requests from "./httpService";

const ProductServices = {
  getAllProducts() {
    return requests.get("/api/products");
  },

  getStockOutProducts() {
    return requests.get("/products/stock-out");
  },

  getProductById(id) {
    return requests.post(`/api/products/${id}`);
  },

  addProduct(body) {
    console.log("body product", body)
    return requests.post("/api/products/add", body);
  },

  addAllProducts(body) {
    console.log("body", body)
    return requests.post("/api/products/product-bulk-upload", body);
  },

  updateProduct(id, body) {
    return requests.put(`/api/products/${id}`, body);
  },

  updateStatus(id, body) {
    return requests.put(`/api/products/status/${id}`, body);
  },

  deleteProduct(id) {
    return requests.delete(`/api/products/${id}`);
  },

  getAllBanners() {
    return requests.get("/product/banners");
  },
  addBanner(body) {
    return requests.post("/product/upload-banners", body);
  },
  updateBanner(id, body) {
    return requests.put(`/product/banner/${id}`, body);
  },
};

export default ProductServices;
