import requests from "./httpService";

const UserServices = {
  getAllUsers(body) {
    return requests.get(`/users?admin=1`, body);
  },
  getUserById(id) {
    return requests.get(`/user/${id}?admin=1`);
  },

  deleteUser(id) {
    return requests.delete(`/user/${id}?admin=1`);
  },
};

export default UserServices;
