import requests from "./httpService";

const EnquiryServices = {
    getAllEnquiries() {
        return requests.get(`/admin-get-enquiries`);
    },
    getUserById(id) {
        return requests.get(`/user/${id}?admin=1`);
    },

    deleteEnquiry(id) {
        return requests.delete(`/admin/enquiry/${id}?admin=1`);
    },
};

export default EnquiryServices;
