import requests from "./httpService";

const BannerServices = {
  deleteBanner(id) {
    return requests.delete(`/banner/deletebanner/${id}`);
  },

  getAllBanners() {
    return requests.get("/api/banners/all-banner");
  },
  getBannerById(id) {
    return requests.get(`/api/banners/${id}`);
  },
  addBanner(body) {
    return requests.post("/api/banners/add", body);
  },
  updateBanner(id, body) {
    return requests.put(`/banner/updateBannner/${id}`, body);
  },
};

export default BannerServices;
